import React from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-cube';
import 'swiper/css/pagination';

import './styles.css';

// import required modules
import { EffectCube, Pagination,Autoplay } from 'swiper/modules';
import Testimonial from '../Testimonial';
import { Grid } from '@mui/material';

export default function Cube() {
  return (
    <Grid container sx={{height:444}} >
      <Swiper
        effect={'cube'}
        grabCursor={true}
        slidesPerView={1}
        cubeEffect={{
          shadow: true,
          slideShadows: true,
          shadowOffset: 20,
          shadowScale: 0.94,
        }}
        speed={4500}
        // coverflowEffect={{
        //   rotate: 50,
        //   stretch: 0,
        //   depth: 100,
        //   modifier: 1,
        //   // slideShadows: true,
        // }}
        
        pagination={true}
        autoplay={{
          delay: 4000,
          disableOnInteraction: false,
        }}
        modules={[EffectCube, Pagination,Autoplay]}
        className="mySwiper"
        style={{  maxWidth: 280,
            height: 300,
            position: "absolute",
            left: "50%",
            top: "50%",
            marginLeft: -140,
            marginTop: -150,}}
      >
        <SwiperSlide>
            <Testimonial
              name={"Diana Shvartz"}
              testimonial={
                "      .     ,   !   "
              }
              avatar={"D.S"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={"ron dishak"}
              testimonial={
                "       .       .         !"
              }
              avatar={"R.D"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={"Noam Younger"}
              testimonial={
                "            !     !  "
              }
              avatar={"N.Y"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={"omer ema"}
              testimonial={`       !      .           .   ,        !`}
              avatar={"O.E"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={"tedy raviv"}
              testimonial={
                "      ,        ."
              }
              avatar={"T.R"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={"limor haviv"}
              testimonial={
                " .        .  "
              }
              avatar={"L.H"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={"Rafa"}
              testimonial={`   .       .
       .
                  .`}
              avatar={"R"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={"Connie Naymark"}
              testimonial={
                "   ,     .  !!"
              }
              avatar={"C.N"}
            />
          </SwiperSlide>
          <SwiperSlide>
            <Testimonial
              name={" "}
              testimonial={
                "        ,  ,         !"
              }
              avatar={"."}
            />
          </SwiperSlide>
     
      </Swiper>
    </Grid>
  );
}
